    box-sizing: border-box;
}

body {
    padding: 0px;
    margin: 0px;

    width: 100vw;
    height: 100vh;

    display: flex;
}

/*Content*/
.player {
    max-width: 99999px;

    padding: 25px;

    flex-grow: 1;
    flex-shrink: 1;
    flex-basis: auto;

    transition:
        flex-grow 300ms ease-in-out,
        max-width 300ms ease-in-out;

    display: flex;
    justify-content: center;
    align-items: center;

    pointer-events: none;
}

.player.focus-field {
    flex-grow: 3;

    pointer-events: auto;
}

.player.lost {
    max-width: 0;
    padding: 0px;
    flex-grow: 0;

    overflow: hidden;
}

.separator {
    border-left: 3px solid gainsboro;
}

.board {
    width: 75%;
    max-width: 75vh;
    max-height: 75vh;
    aspect-ratio: 1 / 1;

    border: 1px solid slategray;

    display: grid;
    grid-template-columns: repeat(9, 1fr);
    grid-template-rows: repeat(9, 1fr);
    grid-auto-columns: 1fr;
    grid-auto-rows: 1fr;

    overflow: hidden;
}

.cell {
    border: 1px solid gainsboro;
    background-color: white;
}

.cell.active:hover {
    display: fixed;
    scale: 1.2;
}

.cell.active:active {
    scale: 0.9;

    transition: scale 50ms ease-in;
}

.cell.miss {
    background-color: slateblue;
}

.cell.hit {
    background-color: palevioletred;

    transition: background-color 300ms ease;
}

.cell.track {
    background-color: mediumseagreen;
}

.cell.hint {
    background-color: rgba(83, 179, 113, 0.6);
    scale: 1.1;

    transition: scale 200ms ease;
}

.cell.invalid {
    background-color: dimgray;
}

/*Menus*/
dialog[open] {
    width: 100vw;
    height: 100vh;

    padding: 0;
    border: 0px transparent;
    margin: 0 auto;
    outline: none;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    background-color: transparent;

    overflow: hidden;
}

dialog::backdrop {
    background-color: rgba(230, 230, 230, 0.8);
}

.dialog-container {
    padding: 24px;
    border-radius: 16px;

    display: flex;
    flex-direction: row;
    justify-content: space-between;

    background-color: whitesmoke;

    user-select: none;
}

.dialog-container > .separator {
    border-width: 2px;
}

.anchor {
    position: relative;
}

.slide-container {
    position: relative;
    display: flex;
    flex-direction: row;

    border-radius: 16px;

    overflow: hidden;
}

.slide-menu {
    position: absolute;

    transform: translateX(125%);

    transition: transform 250ms ease;
}

.slide-menu.open {
    transform: translateX(0%);
}

.menu-button {
    padding: 16px 32px;

    aspect-ratio: 1 / 1;

    transition: background-color 50ms ease-in;
    transition: scale 50ms ease-in;

    display: flex;
    justify-content: stretch;
    align-items: center;
    flex-direction: column;
}

.floater {
    display: flex;
    justify-content: center;
    align-content: center;

    position: absolute;
    transform: translateY(50%);
    bottom: 50%;
    left: -15%;
}

.previous {
    scale: 0;

    padding: 24px;
    border-radius: 50%;

    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
    background-color: whitesmoke;
    background-clip: cover;

    transition: scale 200ms ease;
}

.menu-button:hover {
    background-color: gainsboro;
}

.menu-button:active,
.previous.appear:active {
    scale: 0.9;
}

.menu-button > img {
    height: 128px;

    pointer-events: none;
}

.previous.appear {
    scale: 100%;
}

#vs-com > img {
    content: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
}

#vs-player > img {
    content: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
}

#replay > img {
    content: url(${___CSS_LOADER_URL_REPLACEMENT_3___});
}

#to-title > img {
    content: url(${___CSS_LOADER_URL_REPLACEMENT_4___});
}

#random-board > img {
    content: url(${___CSS_LOADER_URL_REPLACEMENT_5___});
}

#custom-board > img {
    content: url(${___CSS_LOADER_URL_REPLACEMENT_6___});
}

/*Customize UI*/
#customize-ui {
    position: absolute;
    width: 100vw;
    height: 100vh;

    padding: 12px;
    border: 2px solid mediumseagreen;
    border-radius: 8px;

    flex: 0 0 auto;

    background-color: transparent;

    pointer-events: none;

    * {
        pointer-events: auto;
    }
}

#customize-ui.hide {
    display: none;
}

#ready {
    position: fixed;

    padding: 8px 32px 8px 12px;
    border: 1px solid slategray;
    border-radius: 16px 100% 100% 16px;

    outline: 0px;
    background-color: transparent;
    opacity: 40%;
    scale: 0.8;

    transition:
        opacity 200ms ease,
        scale 200ms ease;

    font-size: 1rem;
}

#ready.enabled {
    opacity: 100%;
    scale: 1;
}

#ready.enabled:hover {
    scale: 1.1;
}

#ready.enabled:active {
    scale: 0.9;
}
